@import url(https://fonts.googleapis.com/css2?family=Mate+SC&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: black;
  font-family: 'Mate SC', serif;
  color: white;
}
.header{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.container{
  padding: 5em;

}
.About{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.About img{
  height: 20%;
  width: 20%;
  border-radius: 50%;
  
}
.About p{
  line-height: 3em;
  position: relative;
  right: 10em;
 word-spacing: 0.3em;
}
.name {
  color: #05CE91;
  display: inline-block;
  font-size: 4em;
  font-weight: 100;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
  letter-spacing: 0.1em;
}
.name:hover {
  color: #05CE91;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.profile-pic {
  width: 30%;
  height: 50%;
}
.nav {
  color: white;
  list-style: none;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.nav li {
  margin-top: 2em;
}
.nav a {
  color: white;
  text-decoration: none;
  font-size: 1.2em;
  margin: 1em;
}
.nav a:hover {
  color: #008C8C;
}
.thumbnail {
  height: 10em;
  margin: 1.5em;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}
.thumbnail:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.caption {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.caption p {
  position: relative;
  bottom: 1.2em;
}
.middle {
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.icon {
  margin: 1em;
  color: white;
}
.icon:hover{
  color: #008C8C;
}
.Contact p {
  line-height: 2em;
}
.Contact img{
  width: 20%;
  height: 20%;
}
.Contact{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.address{
  position: relative;
  right: 13em;
}

